          {
            id: 'status_code',
            label: 'Status',
            placeholder: null,
            methods: {
              list: {
                order: true,
                class: { non_active: 'badge badge-danger', active: 'badge badge-primary' },
                transform: 'status_code'
              },
              detail: {
                order: true,
                class: { non_active: 'badge badge-danger', active: 'badge badge-primary' },
                transform: 'status_code'
              },
              create: {
                type: 'radio',
                value: 'active',
                option: {
                  list_pointer: {
                    label: 'label',
                    code: 'code',
                    list: [{ label: 'Aktif', code: 'active' }, { label: 'Non Aktif', code: 'non_active' }]
                  }
                }
              },
              update: {
                type: 'radio',
                value: 'active',
                option: {
                  list_pointer: {
                    label: 'label',
                    code: 'code',
                    list: [{ label: 'Aktif', code: 'active' }, { label: 'Non Aktif', code: 'non_active' }]
                  }
                }
              },
              filter: {
                type: 'radio',
                option: {
                  list_pointer: {
                    label: 'label',
                    code: 'code',
                    list: [{ label: 'Aktif', code: 'active' }, { label: 'Non Aktif', code: 'non_active' }]
                  }
                }
              }
            }
          }
        ]
