<template>
  <div v-if="mainData" class="row">
    <div class="col-md-4">
      <div class="card card-custom mt-5">
        <div class="card-body px-5 position-relative">
          <span class="label label-md label-inline position-absolute" :class="mainData.status_code==='active'? 'label-success': 'label-danger'" style="top:10px;right:10px">{{ mainData.status_code | parse('status_code') }}</span>
          <div class="profile-employee text-center">
            <img v-if="mainData.img_photo" @click="$parent.$parent.getEvent('zoom', mainData.img_photo.url)" v-img-fb="mainData.img_photo.url" alt="img-profile" class="img-round pointer">
            <img v-else v-img-fb="null" alt="img-profile" class="img-round">
            <div class="mt-3">
              <span class="d-block font-weight-bolder font-size-h5 text-dark-75">{{ mainData.fullname }}</span>
              <span class="d-block text-dark-75">{{ mainData.rel_job_position_id || '-' }}</span>
              <span class="d-block text-muted mt-2">{{ mainData.skills || 'Tidak ada keahlian' }}</span>
            </div>
          </div>
          <hr>
          <ul class="navi navi-hover">
            <li v-for="(s,i) in slaveList" :key="i+'-slave'" class="navi-item pointer" @click="setActiveSlave(s)">
              <a class="navi-link" :class="activeSlave.name === s.name ? 'active': ''">
                <span class="symbol symbol-35 mr-3">
                  <span class="symbol-label navi-hover-label bg-light-primary">
                    <i class="text-primary" :class="s.icon"></i>
                  </span>
                </span>
                <div class="navi-text">
                  <span class="d-block font-size-lg font-weight-bolder navi-hover-title">{{ s.name }}</span>
                  <span class="text-muted font-size-sm">{{ s.desc }}</span>
                </div>
                <span class="symbol symbol-25 ml-3">
                  <span class="symbol-label navi-hover-label bg-light-primary">
                    <i class="text-primary ri-arrow-right-line"></i>
                  </span>
                </span>
              </a>
            </li>
          </ul>
          <div class="px-5 mt-3">
            <button @click="$parent.$parent.getEvent('update', mainData)" class="btn btn-block btn-outline-warning">Edit Tenaga Kerja</button>
          </div>
        </div>
      </div>
    </div>
    <div v-if="activeSlave" class="col-md-8">
      <component v-if="activeSlave.component" :is="componentLoader" :id="id"></component>
      <template v-else>
        <div class="card card-custom mt-5">
          <div class="card-body p-5">
            <b-tabs>
              <b-tab title="Biodata Tenaga Kerja" active>
                <div class="p-5">
                  <table class="table no-border">
                    <tr>
                      <th colspan="3" class="font-size-h5 pl-0 pb-0 pt-0">Informasi Pribadi</th>
                    </tr>
                    <tr>
                      <td class="nowrap-table py-1 pl-5">NIK</td>
                      <td class="nowrap-table py-1">:</td>
                      <td class="py-1 text-left"> {{ mainData.nik || '-' }}</td>
                    </tr>
                    <tr>
                      <td class="nowrap-table py-1 pl-5">Nama Lengkap</td>
                      <td class="nowrap-table py-1">:</td>
                      <td class="py-1 text-left"> {{ mainData.fullname || '-' }}</td>
                    </tr>
                    <tr>
                      <td class="nowrap-table py-1 pl-5">Tempat/ Tgl. Lahir</td>
                      <td class="nowrap-table py-1">:</td>
                      <td class="py-1 text-left"> {{ mainData.place_of_birth || '-' }}, {{ mainData.date_of_birth || '-' | parse('longDate') }}</td>
                    </tr>
                    <tr>
                      <td class="nowrap-table py-1 pl-5">Jenis Kelamin</td>
                      <td class="nowrap-table py-1">:</td>
                      <td class="py-1 text-left"> {{ mainData.gender || '-' | parse('kelamin') }}</td>
                    </tr>
                    <tr>
                      <td class="nowrap-table py-1 pl-5">Gol. Darah</td>
                      <td class="nowrap-table py-1">:</td>
                      <td class="py-1 text-left text-uppercase"> {{ mainData.blood_type || '-' }}</td>
                    </tr>
                    <tr>
                      <td class="nowrap-table py-1 pl-5">Agama</td>
                      <td class="nowrap-table py-1">:</td>
                      <td class="py-1 text-left"> {{ mainData.rel_religion_id || '-' }}</td>
                    </tr>
                    <tr>
                      <td class="nowrap-table py-1 pl-5">Kewarganegaraan</td>
                      <td class="nowrap-table py-1">:</td>
                      <td class="py-1 text-left text-uppercase"> {{ mainData.nationality || '-' }}</td>
                    </tr>
                    <tr>
                      <td class="nowrap-table py-1 pl-5">Status Kawin</td>
                      <td class="nowrap-table py-1">:</td>
                      <td class="py-1 text-left"> {{ mainData.marital_status || '-' | parse('kawin') }}</td>
                    </tr>
                    <tr>
                      <td class="nowrap-table py-1 pl-5">Nomor KK</td>
                      <td class="nowrap-table py-1">:</td>
                      <td class="py-1 text-left"> {{ mainData.nomor_kk || '-' }}</td>
                    </tr>
                    <tr>
                      <td class="nowrap-table py-1 pl-5">NPWP</td>
                      <td class="nowrap-table py-1">:</td>
                      <td class="py-1 text-left"> {{ mainData.npwp || '-' }}</td>
                    </tr>
                    <tr>
                      <th colspan="3" class="font-size-h5 pl-0 pb-0 pt-5">Visi</th>
                    </tr>
                    <tr>
                      <td class="py-1 pl-5" colspan="3">{{ mainData.personal_visi_description || '-' }}</td>
                    </tr>
                    <tr>
                      <th colspan="3" class="font-size-h5 pl-0 pb-0 pt-5">Personal Value</th>
                    </tr>
                    <tr>
                      <td class="py-1 pl-5" colspan="3">{{ mainData.personal_value_description || '-' }}</td>
                    </tr>
                    <tr>
                      <th colspan="3" class="font-size-h5 pl-0 pb-0 pt-5">Passion Interest</th>
                    </tr>
                    <tr>
                      <td class="py-1 pl-5" colspan="3">{{ mainData.passion_interest || '-' }}</td>
                    </tr>
                  </table>
                </div>
              </b-tab>
              <b-tab title="Pendidikan &amp; Pekerjaan">
                <div class="p-5">
                  <table class="table no-border">
                    <tr>
                      <th colspan="3" class="font-size-h5 pl-0 pb-0 pt-5">Pendidikan</th>
                    </tr>
                    <tr>
                      <td class="nowrap-table py-1 pl-5">Gelar Depan</td>
                      <td class="nowrap-table py-1">:</td>
                      <td class="py-1 text-left"> {{ mainData.gelar_depan || '-' }}</td>
                    </tr>
                    <tr>
                      <td class="nowrap-table py-1 pl-5">Gelar Belakang</td>
                      <td class="nowrap-table py-1">:</td>
                      <td class="py-1 text-left"> {{ mainData.gelar_belakang || '-' }}</td>
                    </tr>
                    <tr>
                      <td class="nowrap-table py-1 pl-5">Pendidikan Terakhir</td>
                      <td class="nowrap-table py-1">:</td>
                      <td class="py-1 text-left"> {{ mainData.rel_education_id || '-' }}</td>
                    </tr>
                    <tr>
                      <td class="nowrap-table py-1 pl-5">Program Studi</td>
                      <td class="nowrap-table py-1">:</td>
                      <td class="py-1 text-left"> {{ mainData.program_study || '-' }}</td>
                    </tr>
                    <tr>
                      <td class="nowrap-table py-1 pl-5">Institusi</td>
                      <td class="nowrap-table py-1">:</td>
                      <td class="py-1 text-left"> {{ mainData.institution_name || '-' }}</td>
                    </tr>
                    <tr>
                      <td class="nowrap-table py-1 pl-5">Keahlian</td>
                      <td class="nowrap-table py-1">:</td>
                      <td class="py-1 text-left"> {{ mainData.skills || '-' }}</td>
                    </tr>
                    <tr>
                      <th colspan="3" class="font-size-h5 pl-0 pb-0 pt-5">Pekerjaan</th>
                    </tr>
                    <tr>
                      <td class="nowrap-table py-1 pl-5">NIP (Nomor Induk Pegawai)</td>
                      <td class="nowrap-table py-1">:</td>
                      <td class="py-1 text-left"> {{ mainData.nip || '-' }}</td>
                    </tr>
                    <tr>
                      <td class="nowrap-table py-1 pl-5">Jabatan</td>
                      <td class="nowrap-table py-1">:</td>
                      <td class="py-1 text-left"> {{ mainData.rel_job_position_id || '-' }}</td>
                    </tr>
                     <tr>
                      <td class="nowrap-table py-1 pl-5">PLT (Pelaksana Tugas)</td>
                      <td class="nowrap-table py-1">:</td>
                      <td class="py-1 text-left"> {{ mainData.is_plt || '-' | parse('is_plt') }}</td>
                    </tr>
                    <tr>
                      <td class="nowrap-table py-1 pl-5">MPP (Masa Pra Pensiun)</td>
                      <td class="nowrap-table py-1">:</td>
                      <td class="py-1 text-left"> {{ mainData.is_mpp || '-' | parse('is_mpp') }}</td>
                    </tr>
                    <tr>
                      <td class="nowrap-table py-1 pl-5">Status Ketenagakerjaan</td>
                      <td class="nowrap-table py-1">:</td>
                      <td class="py-1 text-left"> {{ mainData.rel_status_employment_id || '-' }}</td>
                    </tr>
                    <tr>
                      <td class="nowrap-table py-1 pl-5">Tanggal Bergabung</td>
                      <td class="nowrap-table py-1">:</td>
                      <td class="py-1 text-left"> {{ mainData.entry_date || '-' | parse('longDate') }}</td>
                    </tr>
                    <tr>
                      <td class="nowrap-table py-1 pl-5">Tanggal Pengangkatan</td>
                      <td class="nowrap-table py-1">:</td>
                      <td class="py-1 text-left"> {{ mainData.appointment_date || '-' | parse('longDate') }}</td>
                    </tr>
                    <tr>
                      <td class="nowrap-table py-1 pl-5">Tanggal Keluar</td>
                      <td class="nowrap-table py-1">:</td>
                      <td class="py-1 text-left"> {{ mainData.out_date || '-' | parse('longDate') }}</td>
                    </tr>
                    <tr>
                      <th colspan="3" class="font-size-h5 pl-0 pb-0 pt-5">BPJS</th>
                    </tr>
                    <tr>
                      <td class="nowrap-table py-1 pl-5">No. BPJS Ketenagakerjaan</td>
                      <td class="nowrap-table py-1">:</td>
                      <td class="py-1 text-left"> {{ mainData.nomor_kpj_bpjs_tk || '-' }}</td>
                    </tr>
                    <tr>
                      <td class="nowrap-table py-1 pl-5">No. BPJS Kesehatan</td>
                      <td class="nowrap-table py-1">:</td>
                      <td class="py-1 text-left"> {{ mainData.nomor_bpjs_kes || '-' }}</td>
                    </tr>
                    <tr>
                      <th colspan="3" class="font-size-h5 pl-0 pb-0 pt-5">Integrasi</th>
                    </tr>
                    <tr>
                      <td class="nowrap-table py-1 pl-5">Username HRM</td>
                      <td class="nowrap-table py-1">:</td>
                      <td class="py-1 text-left"> {{ mainData.rel_user_id || '-' }}</td>
                    </tr>
                    <tr>
                      <td class="nowrap-table py-1 pl-5">Taptap User ID</td>
                      <td class="nowrap-table py-1">:</td>
                      <td class="py-1 text-left"> {{ mainData.taptap_user_id || '-' }}</td>
                    </tr>
                  </table>
                </div>
              </b-tab>
              <b-tab title="Kontak &amp; Sosial Media">
                <div class="p-5">
                  <table class="table no-border">
                    <tr>
                      <th colspan="3" class="font-size-h5 pl-0 pb-0 pt-5">Informasi Kontak</th>
                    </tr>
                    <tr>
                      <td class="nowrap-table py-1 pl-5">Kontak Darurat</td>
                      <td class="nowrap-table py-1">:</td>
                      <td class="py-1 text-left"> {{ mainData.emergency_contact || '-' }}</td>
                    </tr>
                    <tr>
                      <td class="nowrap-table py-1 pl-5">Alamat</td>
                      <td class="nowrap-table py-1">:</td>
                      <td class="py-1 text-left"> {{ mainData.address || '-' }}</td>
                    </tr>
                    <tr>
                      <td class="nowrap-table py-1 pl-5">Nomor Telp.</td>
                      <td class="nowrap-table py-1">:</td>
                      <td class="py-1 text-left"> {{ mainData.telephone || '-' }}</td>
                    </tr>
                    <tr>
                      <td class="nowrap-table py-1 pl-5">Email</td>
                      <td class="nowrap-table py-1">:</td>
                      <td class="py-1 text-left"> {{ mainData.email || '-' }}</td>
                    </tr>
                    <tr>
                      <th colspan="3" class="font-size-h5 pl-0 pb-0 pt-5">Sosial Media</th>
                    </tr>
                    <tr>
                      <td class="nowrap-table py-1 pl-5">Facebook</td>
                      <td class="nowrap-table py-1">:</td>
                      <td class="py-1 text-left"> {{ mainData.facebook_name || '-' }}</td>
                    </tr>
                    <tr>
                      <td class="nowrap-table py-1 pl-5">Instagram</td>
                      <td class="nowrap-table py-1">:</td>
                      <td class="py-1 text-left"> {{ mainData.instagram_name || '-' }}</td>
                    </tr>
                    <tr>
                      <td class="nowrap-table py-1 pl-5">Twitter</td>
                      <td class="nowrap-table py-1">:</td>
                      <td class="py-1 text-left"> {{ mainData.twitter_name || '-' }}</td>
                    </tr>
                    <tr>
                      <td class="nowrap-table py-1 pl-5">LinkedIn</td>
                      <td class="nowrap-table py-1">:</td>
                      <td class="py-1 text-left"> {{ mainData.linkedin_name || '-' }}</td>
                    </tr>
                  </table>
                </div>
              </b-tab>
            </b-tabs>
          </div>
        </div>
      </template>
    </div>
  </div>
</template>

<style scoped>
  .navi-link:hover .navi-hover-label,
  .navi-link.active .navi-hover-label {
    background-color: var(--primary-color) !important;
  }
  .navi-link:hover .navi-hover-label i,
  .navi-link.active .navi-hover-label i {
    color: #fff !important;
  }
  .navi-link:hover .navi-hover-title,
  .navi-link.active .navi-hover-title {
    color: var(--primary-color);
  }
  .profile-employee img.img-round {
    width: 150px;
    height: 150px;
    border-radius: 50%;
    object-fit: cover;
    object-position: center center;
  }
</style>

<script>
export default {
  name: 'detail-employees',
  props: {
    id: { required: true, type: Number }
  },
  data () {
    return {
      title: 'hehe',
      mainData: null,
      activeSlave: null,
      slaveList: [
        { icon: 'ri-user-line', name: 'Data Diri', desc: 'Biodata Tenaga Kerja', component: null },
        { icon: 'ri-group-line', name: 'Keluarga', desc: 'Daftar Keluarga Tenaga Kerja', component: 'ketenagakerjaan/employee-families' },
        { icon: 'ri-book-mark-line', name: 'Riwayat Pendidikan', desc: 'Daftar Pendidikan Tenaga Kerja', component: 'ketenagakerjaan/employee-educational-histories' },
        { icon: 'ri-book-mark-line', name: 'Riwayat Pekerjaan', desc: 'Daftar Pekerjaan Tenaga Kerja', component: 'ketenagakerjaan/employee-work-experiences' },
        { icon: 'ri-book-mark-line', name: 'Riwayat Jabatan', desc: 'Daftar Jabatan Tenaga Kerja', component: 'ketenagakerjaan/employee-job-position-histories' },
        { icon: 'ri-shield-star-line', name: 'Prestasi & Penghargaan', desc: 'Daftar Pencapaian Tenaga Kerja', component: 'ketenagakerjaan/employee-achievement-awards' },
        { icon: 'ri-briefcase-line', name: 'KPI', desc: 'Performa Tenaga Kerja', component: 'ketenagakerjaan/employee-kpi' },
        { icon: 'ri-file-list-3-line', name: 'Sertifikasi', desc: 'Sertifikasi Tenaga Kerja', component: 'ketenagakerjaan/employee-certifications' },
        { icon: 'ri-folder-user-line', name: 'Dokumen', desc: 'File Dokumen Pribadi', component: 'ketenagakerjaan/employee-documents' }
      ]
    }
  },
  computed: {
    componentLoader () {
      if (this.activeSlave.component) {
        let loader = this.activeSlave.component
        return () => import(`@/modules/${loader}.vue`)
      } else return null
    }
  },
  beforeMount () {
    this.setActiveSlave(this.slaveList[0])
  },
  async mounted () {
    const mainData = await this.getAPI('get', 'employees/' + this.id + '/show')
    this.mainData = await Object.assign({}, mainData)
  },
  methods: {
    getAPI (method, api, param, obj = null) {
      return this.$_api[method](api, param).then(res => {
        return obj ? res.data[obj] || res.data : res.data
      }).catch((err) => {
        this.$_alert.error(err)
      })
    },
    setActiveSlave (e) {
      this.activeSlave = Object.assign({}, e)
      if (e.component) this.$store.commit('set', ['activeSlave', { master: this.title, param: 'employee_id', key: this.id }, true])
    }
  }
}
</script>



